var confirmDirective = () => {

    return {
        restrict: 'A',
        templateUrl: 'modules/confirm/views/confirm.html',
        transclude: true,
        replace: true,
        scope: {
            ccConfirm: '&'
        },
        link: (scope) => {

            scope.turnOn = false;

            scope.approve = () => {

                scope.ccConfirm();

            };

            scope.cancel = () => {

                scope.turnOn = false;

            };

            scope.confirm = () => {

                scope.turnOn = true;

            };

        }
    };

};

export default confirmDirective;
