/**
 * Convert params for request
 *
 * @name ParamConverter
 */
class ParamConverter {
    /**
     * Convert list resource parameters
     *
     * @param {Object} params
     * @returns {{limit: (number), offset: number}}
     */
    list (params) {

        return {
            limit: params.count,
            offset: (params.page - 1) * params.count
        };

    }

}

export default ParamConverter;
