var conditionFactory = () => {

    /**
     * @name Condition
     * @description Condition entity
     */
    class Condition {

        constructor () {

            /**
             * @type {number}
             */
            this.id = undefined;

            /**
             * @type {string}
             */
            this.variable = undefined;

            /**
             * @type {string}
             */
            this.condition = undefined;

            /**
             * @type {string}
             */
            this.value = undefined;

        }

    }

    return Condition;

};


export default conditionFactory;
